import React from 'react';
import ReactDOM from 'react-dom';
import {CellButton, View, Panel, PanelHeader, Group, FormLayout, Textarea, Div, Button } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import connect from '@vkontakte/vkui-connect';

connect.send("VKWebAppInit", {}); 
console.log(connect.send("VKWebAppGetPhoneNumber", {}));

class Example extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activePanel: 'panel1'
    }
  }

  render() {
    return (
      <View activePanel={this.state.activePanel}>
        <Panel id="panel1">
          <PanelHeader>Главная</PanelHeader>
          <Group>
            <CellButton onClick={() => this.setState({ activePanel: 'panel2' })}>
              Создать
            </CellButton>
          </Group>
        </Panel>
        <Panel id="panel2">
          <PanelHeader>Panel 2</PanelHeader>
          <Group>
            <CellButton onClick={ () => this.setState({ activePanel: 'panel1' }) }>
              Go to panel 1
            </CellButton>
            <FormLayout>
              <Textarea top="Сумма" placeholder="Сумма" />
            </FormLayout>
            <Div>
              <Button onClick={ () => this.setState({ activePanel: 'panel3' }) } size="xl" level="secondary">Extra large</Button>
            </Div>
          </Group>
        </Panel>
        <Panel id="panel3">
          <PanelHeader>Главная</PanelHeader>
          <Group>
            <CellButton onClick={ () => this.setState({ activePanel: 'panel1' }) }>
              c
            </CellButton>
          </Group>
        </Panel>
      </View>
    )
  }
}

ReactDOM.render(<Example />,
      document.getElementById('root')
    );


